import React from "react";
import './App.css';
import Navigation from "./components/navigation/Navigation";
import {Routers, RoutersLog} from "./route/Routers";
import Header from "./components/Header";
import Footer from "./components/Footer";
import ContentHeader from "./components/ContentHeader";
import {Link} from "react-router-dom";
import {sendRequest} from "./api/sendRequest";
import {apis} from "./configuration/configurationApi";


export default function App(props) {

    const [data, setData] = React.useState({
        isSimpleNav: false
    });

    const [logo, setLogo] = React.useState({
        favicon: "",
        footer_text: "",
        icon: "",
        last_loc: "",
        live_chat: "",
        name: "",
        number_limit: 0,
        set_id: 0
    });

    React.useEffect(() => {
        let body = {
            method: "get",
        };
        const res1 = sendRequest(apis.base + apis.logo, body, ((res) => callback(res)));
    }, []);

    const callback = (res) => {
        setLogo(res.data[0])
    };



    const isLoggedIn = () => {
        return localStorage.getItem("loginUser") !== "null"
    };


    if (!isLoggedIn()) {
        return (
            <RoutersLog/>
        )
    } else {
        return (
            <div className={data.isSimpleNav ? "app app-sidebar-minified" : "app"}>
                <div className="app-header">
                    <Header email={"mails@thesupercity.com"}
                            setSimpleNavState={() => setData({...data, isSimpleNav: !data.isSimpleNav})}/>
                </div>
                <div className="app-sidebar">
                    <Navigation isSimpleNav={data.isSimpleNav}/>
                </div>
                <div className="app-content">
                    <ContentHeader logo={logo}/>
                    <div className="container-fluid">
                        <Routers/>
                    </div>
                </div>
                <div className="app app-footer-fixed">
                    <Footer logo={logo}/>
                </div>
                <Link href="#" data-click="scroll-top" className="btn-scroll-top fade">
                    <i className="fa fa-arrow-up"/>
                </Link>
            </div>
        )
    }
}


