import React from "react";
import SendNotification from "../../components/SendNotification";
import {sendRequest} from "../../api/sendRequest";
import {apis} from "../../configuration/configurationApi";

export default function SendNotificationToDriver(props) {


    const [data, setData] = React.useState({});
    React.useEffect(() => {
        let body = {
            method: "get",
        };
        const res = sendRequest(apis.base + apis.notificationDrivers, body, ((res) => callback(res)));
    }, []);

    const callback = (res) => {
        setData(res.data)
    };


    return (
        <div className="card">
            <div className="card-header card-header-primary">
                <h4 className="card-title">Notification to Driver</h4>
            </div>
            <SendNotification
                data={data} api={apis.notificationToDriver} primaryKey={"dboy_id"}
                sendTo={"Driver"}
            />
        </div>
    )
}