import React from "react";

export default function ContentHeader(props) {


    const [data, setData] = React.useState({
        theme: "primary"
    });
    const themes = ["English", "Bulgarian", "Hindi", "Chinese"];

    const setTheme = (event) => {
        setData({theme: event.target.value});
    };

    return (
        <div>
            <div className="row">
                <div className="col-md-7">
                    <h1 className="page-header mb-3">
                        Hi, {props.logo.name}. <small>Here is your admin panel.</small>
                    </h1>
                </div>
                <div className="col-md-5" align="right">
                    <div className="col-md-4" align="right">
                        <select className="form-control" value={data.theme}
                                onChange={setTheme}>
                            {themes.map(theme =>
                                <option key={theme} value={theme}>{theme}</option>)}
                        </select>
                    </div>
                </div>
            </div>
        </div>
    )

}