import React from "react";import {useLocation} from "react-router";import {example} from "../../data/examData";
import dashboardImg from "../../asset/img/page/dashboard.svg";
import {routers} from "../../configuration/configurationUI";

export default function Forgot(props) {

    const item = useLocation().state;
    const [data, setData] = React.useState({
        email: "",
    });


    const submit = () => {
        console.log(data.email);
        window.location.href = "/";
    };


    return (
        <div className="app app-full-height app-without-header">
            <div className="login">
                <div className="login-content">
                    <div align="center">
                        <img align="center" img="" style={{width: "100px", height: "auto"}}
                             src="http://localhost/Aditya/adminpanel/images/app_logo/app_icon/04-01-2022/appicon_512x512.png"
                             alt="IMG"/>
                    </div>
                    <hr/>
                    <h1 className="text-center">Change Password</h1>
                    <div className="form-group">
                        <label>Email Address</label>
                        <input onChange={(event) => data.email = event.target.value} type="email"
                               placeholder="input Email"
                               className="form-control"/><br/><br/>

                    </div>
                    <button onClick={submit} className="btn btn-primary btn-lg btn-block fw-500 mb-3">
                        Send Password Reset Mail
                    </button>

                </div>
            </div>
            <a href="#" data-click="scroll-top" className="btn-scroll-top fade"><i
                className="fa fa-arrow-up"/></a>
        </div>
    )
}