import React from "react";import {useLocation} from "react-router";
import {routers} from "../../configuration/configurationUI";
import NavItem from "./NavItem";
import "../../configuration/configurationUI";

export default function Navigation(props) {

    const [opened, setOpened]=React.useState([]);

    const setIndex = (index, stateOpen) => {
        let open = [];
        for (let i = 0; i <= 21; i++) {
            open[i] = false;
        }
        open[index] = stateOpen;
        setOpened(open);
    };


    return (
        <div className="app-sidebar-content nav-scroll">
            <div className="menu">
                <div className="menu-header">
                    Navigation
                </div>
                <NavItem
                    title={"Dashboard"}
                    mainTab={routers.home}
                    icon={"fa fa-laptop"}
                    tabs={{}}
                    setOpenIndex={(stateOpen) => setIndex(0, stateOpen)}
                    open={opened[0]}
                    isSimpleNav={props.isSimpleNav}
                />
                <NavItem
                    title={"Sub-Admin Management"}
                    mainTab={""}
                    icon={"fas fa-user-astronaut"}
                    tabs={{
                        "Roles": routers.roles,
                        "Sub-Admin": routers.subAdminList
                    }}
                    setOpenIndex={(stateOpen) => setIndex(1, stateOpen)}
                    open={opened[1]}
                    isSimpleNav={props.isSimpleNav}
                />
                <NavItem
                    title={"Taxes"}
                    mainTab={routers.taxList}
                    icon={"fa fa-hashtag"}
                    tabs={{}}
                    setOpenIndex={(stateOpen) => setIndex(2, stateOpen)}
                    open={opened[2]}
                    isSimpleNav={props.isSimpleNav}
                />
                <NavItem
                    title={"ID"}
                    mainTab={routers.idList}
                    icon={"fa fa-credit-card"}
                    tabs={{}}
                    setOpenIndex={(stateOpen) => setIndex(3, stateOpen)}
                    open={opened[3]}
                    isSimpleNav={props.isSimpleNav}
                />
                <NavItem
                    title={"Membership Plans"}
                    mainTab={routers.membershipList}
                    icon={"fa fa-tag"}
                    tabs={{}}
                    setOpenIndex={(stateOpen) => setIndex(4, stateOpen)}
                    open={opened[4]}
                    isSimpleNav={props.isSimpleNav}
                />
                <NavItem
                    title={"Reports"}
                    mainTab={""}
                    icon={"fa fa-cubes"}
                    tabs={{
                        "Item Requirement(Day-Wise)": routers.reportItemSaleByStore,
                        "Item Sales Report(Last 30 Days)": routers.reportTotalItemSalesLast30Days,
                        "Tax Reports": routers.reportTax,
                        "Reports": routers.reportOrder,
                    }}
                    setOpenIndex={(stateOpen) => setIndex(5, stateOpen)}
                    open={opened[5]}
                    isSimpleNav={props.isSimpleNav}
                />
                <NavItem
                    title={"Send Notifications"}
                    mainTab={""}
                    icon={"fa fa-bell"}
                    tabs={{
                        "Send Notification to Users": routers.notificationToUser,
                        "Send Notification to Store": routers.notificationToStore,
                        "Send Notification to Driver": routers.notificationToDriver,
                    }}
                    setOpenIndex={(stateOpen) => setIndex(6, stateOpen)}
                    open={opened[6]}
                    isSimpleNav={props.isSimpleNav}
                />
                <NavItem
                    title={"List Notifications"}
                    mainTab={""}
                    icon={"fa fa-bell"}
                    tabs={{
                        "User Notifications": routers.notificationListUser,
                        "Store Notifications": routers.notificationListStore,
                        "Driver Notifications": routers.notificationListDriver,
                    }}
                    setOpenIndex={(stateOpen) => setIndex(7, stateOpen)}
                    open={opened[7]}
                    isSimpleNav={props.isSimpleNav}
                />
                <NavItem
                    title={"Users Management"}
                    mainTab={""}
                    icon={"fa fa-users"}
                    tabs={{
                        "Users Data": routers.userList,
                        "Wallet Recharge History": routers.usersWalletRechargeHistory,
                    }}
                    setOpenIndex={(stateOpen) => setIndex(8, stateOpen)}
                    open={opened[8]}
                    isSimpleNav={props.isSimpleNav}
                />
                <NavItem
                    title={"Category Management"}
                    mainTab={""}
                    icon={"fa fa-cubes"}
                    tabs={{
                        "Parent Category": routers.categoryList,
                        "Sub Categories": routers.subCategoryList,
                    }}
                    setOpenIndex={(stateOpen) => setIndex(9, stateOpen)}
                    open={opened[9]}
                    isSimpleNav={props.isSimpleNav}
                />
                <NavItem
                    title={"Product Catalog"}
                    mainTab={""}
                    icon={"fa fa-cubes"}
                    tabs={{
                        "Admin Products": routers.productList,
                        "Store Products(request)": routers.storeProductsList,
                        "Trending Search": routers.trendingSearchProductAdd,
                        "Bulk Upload": routers.bulkUpload,
                    }}
                    setOpenIndex={(stateOpen) => setIndex(10, stateOpen)}
                    open={opened[10]}
                    isSimpleNav={props.isSimpleNav}
                />
                <NavItem
                    title={"Area Management"}
                    mainTab={""}
                    icon={"fa fa-map"}
                    tabs={{
                        "Cities": routers.cityList,
                        "Area/Society": routers.societyList,
                        "Bulk Upload": routers.areaBulkUploadCitySociety,
                    }}
                    setOpenIndex={(stateOpen) => setIndex(11, stateOpen)}
                    open={opened[11]}
                    isSimpleNav={props.isSimpleNav}
                />
                <NavItem
                    title={"Store Management"}
                    mainTab={""}
                    icon={"fa fa-building"}
                    tabs={{
                        "Store List": routers.adminStoreList,
                        "Store Earning/Payments": routers.storesFinance,
                        "Store Approval": routers.storesWaitingForApprovalStoreList,
                    }}
                    setOpenIndex={(stateOpen) => setIndex(12, stateOpen)}
                    open={opened[12]}
                    isSimpleNav={props.isSimpleNav}
                />
                <NavItem
                    title={"Orders Management"}
                    mainTab={""}
                    icon={"fa fa-calendar"}
                    tabs={{
                        "Reject By Store": routers.cancelledOrders,
                        "All Orders": routers.adminAllOrders,
                        "Pending Orders": routers.adminPendingOrders,
                        "Cancelled Orders": routers.adminCancelledOrders,
                        "Ongoing Orders": routers.adminOngoingOrders,
                        "Out For Delivery Orders": routers.adminOutForDeliveryOrders,
                        "Payment Failed Orders": routers.adminPaymentFailedOrders,
                        "Completed Orders": routers.adminCompleteOrders,
                        "Day Wise Orders": routers.ordersTodayAll,
                        "Missed Orders": routers.storeMissedOrders,
                    }}
                    setOpenIndex={(stateOpen) => setIndex(13, stateOpen)}
                    open={opened[13]}
                    isSimpleNav={props.isSimpleNav}
                />
                <NavItem
                    title={"Payout"}
                    mainTab={""}
                    icon={"fa fa-check"}
                    tabs={{
                        "Payout Requests": routers.payoutReq,
                        "Payout Validation": routers.prv,
                    }}
                    setOpenIndex={(stateOpen) => setIndex(14, stateOpen)}
                    open={opened[14]}
                    isSimpleNav={props.isSimpleNav}
                />
                <NavItem
                    title={"Rewards"}
                    mainTab={""}
                    icon={"fa fa-trophy"}
                    tabs={{
                        "Rewards": routers.reward,
                        "Redeem Value": routers.redeem,
                    }}
                    setOpenIndex={(stateOpen) => setIndex(15, stateOpen)}
                    open={opened[15]}
                    isSimpleNav={props.isSimpleNav}
                />
                <NavItem
                    title={"Delivery Boy"}
                    mainTab={""}
                    icon={"fa fa-users"}
                    tabs={{
                        "Delivery Boy": routers.dBoyList,
                        "Delivery Boy Incentive": routers.dBoyIncentive,
                    }}
                    setOpenIndex={(stateOpen) => setIndex(16, stateOpen)}
                    open={opened[16]}
                    isSimpleNav={props.isSimpleNav}
                />
                <NavItem
                    title={"Pages"}
                    mainTab={""}
                    icon={"fa fa-bookmark"}
                    tabs={{
                        "About Us": routers.aboutUs,
                        "Terms & Conditions": routers.terms,
                    }}
                    setOpenIndex={(stateOpen) => setIndex(17, stateOpen)}
                    open={opened[17]}
                    isSimpleNav={props.isSimpleNav}
                />
                <NavItem
                    title={"Feedback"}
                    mainTab={""}
                    icon={"fa fa-comment"}
                    tabs={{
                        "Users Feedback": routers.userFeedbackList,
                        "Store Feedback": routers.storeFeedbackList,
                        "Delivery Boy Feedback": routers.driverFeedbackList,
                    }}
                    setOpenIndex={(stateOpen) => setIndex(18, stateOpen)}
                    open={opened[18]}
                    isSimpleNav={props.isSimpleNav}
                />
                <NavItem
                    title={"Callback Requests"}
                    mainTab={""}
                    icon={"fa fa-phone"}
                    tabs={{
                        "Users Callback Requests": routers.userCallbackRequests,
                        "Stores Callback Requests": routers.storeCallbackRequests,
                        "Delivery Boy Callback Requests": routers.driverCallbackRequests,
                    }}
                    setOpenIndex={(stateOpen) => setIndex(19, stateOpen)}
                    open={opened[19]}
                    isSimpleNav={props.isSimpleNav}
                />
                <div className="menu-header">
                    Settings
                </div>
                <NavItem
                    title={"Settings"}
                    mainTab={routers.globalSetting}
                    icon={"fa fa-cog"}
                    tabs={{}}
                    setOpenIndex={(stateOpen) => setIndex(20, stateOpen)}
                    open={opened[20]}
                    isSimpleNav={props.isSimpleNav}
                />
                <NavItem
                    title={"Cancelling Reasons"}
                    mainTab={routers.cancellingReasonsList}
                    icon={"fa fa-list"}
                    tabs={{}}
                    setOpenIndex={(stateOpen) => setIndex(21, stateOpen)}
                    open={opened[21]}
                    isSimpleNav={props.isSimpleNav}
                />
            </div>
        </div>
    )
}