import React from "react";import {useLocation} from "react-router";import {example} from "../../data/examData";

export default function ChangePassword(props) {
    const item = useLocation().state;


    const [data, setData] = React.useState({
        data: {
            role_name: 1,
            dashboard: 1,
            membership: 0,
            category: 1,
            tax: 1,
            id: 0,
            report: 1,
        },
        passwordOld: "",
        passwordNew: "",
    });
    const submit = () => {
        console.log(data.passwordOld, data.passwordNew)
    };


    return (
        <div className="card">
            <div className="card-header card-header-primary">
                <h4 className="card-title">Change Password</h4>
            </div>
            <div className="card-body">
                <div className="form-group">
                    <label>Current Password</label>
                    <input type="text" onChange={(event) => data.passwordOld = event.target.value}
                           className="form-control"/>
                </div>
                <div className="form-group">
                    <label>New Password</label>
                    <input type="text" onChange={(event) => data.passwordNew = event.target.value}
                           className="form-control"/>
                </div>
                <br/>
                <button onClick={submit} type="submit" className="btn btn-primary pull-center mr-1">
                    Update Password
                </button>
            </div>
        </div>

    )
}