import React from "react";import {useLocation} from "react-router";import {example} from "../../data/examData";
import logo from "../../asset/img/logo.png";


export default function EditProfile(props) {
    const item = useLocation().state;


    const [data, setData] = React.useState({
        roles: example.products,
        data: {
            role_name: 1,
            dashboard: 1,
            membership: 0,
            category: 1,
            tax: 1,
            id: 0,
            report: 1,
        },

        name: "aa",
        email: "",
        image: "",
        roleId: 1,
        password: "a"

    });


    const submit = () => {
        console.log(data.data)
    };


    return (
        <div className="card">
            <div className="card-header card-header-primary">
                <h4 className="card-title">Edit Profile</h4>
            </div>
            <div className="card-body">
                <div className="row form-group">
                    <div className="col-md-6">
                        <label>Admin Name</label>
                        <input type="text" value={data.name}
                               onChange={(event) => data.name = event.target.value}
                               className="form-control"/>
                    </div>
                    <div className="col-md-6">
                        <label>Admin Email</label>
                        <input type="number" value={data.email}
                               onChange={(event) => data.email = event.target.value}
                               className="form-control"/>
                    </div>
                </div>
                <div className="form-group">
                    <img src={logo} style={{height: "100px", width: "100px"}}/>
                </div>
                <div className="form-group">
                    <div className="custom-file">
                        <div className="custom-file">
                            <input type="file" className="custom-file-input" accept="image/*"
                                   onChange={(event) => data.image = event.target.value}/>
                            <label className="custom-file-label" htmlFor="customFile">Choose Admin Profile</label>
                        </div>
                    </div>
                </div>

                <br/>
                <button onClick={submit} className="btn btn-primary pull-center mr-1">
                    Update Profile
                </button>
            </div>
        </div>

    )
}