import React from "react";
import {useLocation} from "react-router";import {example} from "../../data/examData";
import {Link} from "react-router-dom";
import {routers} from "../../configuration/configurationUI";

export default function Login(props) {

    const item = useLocation().state;
    const [data, setData] = React.useState({
        email: "",
        password: "",
    });


    const submit = () => {
        console.log(data.email, data.password)
        localStorage.setItem("loginUser", "a")
        window.location.href = "/";
    };


    return (
        <div id="app" className="app app-full-height app-without-header">
            <div className="login">
                <div className="login-content">
                    <div align="center">
                        <img align="center" img="" style={{width: "100px", height: "auto"}}
                             src="http://localhost/Aditya/adminpanel/images/app_logo/app_icon/04-01-2022/appicon_512x512.png"
                             alt="IMG"/>
                    </div>
                    <hr/>
                    <h1 className="text-center">Sign In</h1>
                    <div className="text-muted text-center mb-4">
                        Admin/Sub-Admin Login.
                    </div>
                    <div className="form-group">
                        <label>Email Address</label>
                        <input data-validate="Valid email is required: ex@abc.xyz" type="email"
                               onChange={(event) => data.email = event.target.value}
                               className="form-control form-control-lg fs-15px" name="email"
                               placeholder="username@address.com"/>
                    </div>
                    <div className="form-group">
                        <div className="d-flex">
                            <label>Password</label>
                            <Link to={routers.forgot}
                                  className="ml-auto text-muted">Forgot password?</Link>
                        </div>
                        <input type="password" className="form-control form-control-lg fs-15px"
                               onChange={(event) => data.password = event.target.value}
                               placeholder="Enter your password"/>
                    </div>
                    <div className="form-group">
                        <div className="custom-control custom-checkbox">
                            <input className="custom-control-input" type="checkbox" value=""
                                   id="customCheck1"/>
                            <label className="custom-control-label fw-500" htmlFor="customCheck1">
                                Remember me</label>
                        </div>
                    </div>
                    <button onClick={submit} className="btn btn-primary btn-lg btn-block fw-500 mb-3">
                        Sign In
                    </button>
                </div>
            </div>
        </div>
    )
}